.box-animate {
  background-color: transparent;
  border-radius: 3px;
  color: #fff;
  position: absolute;
  /* top: 50%; */
  /* left: 50%; */
  transform: translate(-00%, -0%);
  width: 350px;
  height: 300px;
  transform-style: preserve-3d;
  perspective: 2000px;
  transition: 0.4s;
  text-align: center;
  padding: 3%;
}
.box-animate:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  border-top: 20px solid #3ad4af;
  border-left: 20px solid#3ad4af;
  box-sizing: border-box;
}
.box-animate:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-bottom: 20px solid #3ad4af;
  border-right: 20px solid #3ad4af;
  box-sizing: border-box;
}

.box-animate .text-animate {
  position: absolute;
  top: 30px;
  left: -30px;
  width: calc(100% + 60px);
  height: calc(100% - 60px);
  background-color: #2c3a47;
  border-radius: 3px;
  transition: 0.4s;
}

.box-animate .text-animate div {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  text-align: center;
  width: 100%;
  padding: 30px 50px;
  line-height: 1.5;
  box-sizing: border-box;
}
.box-animate .text-animate div h3 {
  font-size: 0.9rem;
  font-weight: 500;
  /* margin-bottom: 25px; */
}
.box-animate .text-animate div p {
  font-size: 0.5rem;
}
.box-animate:hover {
  /* transform: translate(-50%, -50%) rotateY(-20deg) skewY(3deg); */
}
.box-animate:hover .text-animate {
  transform: rotateY(20deg) skewY(-3deg);
}
.text-animate a {
  text-decoration: none;
