.control-container {
  background-image: url(../../assests/sad.png);
  background-repeat: no-repeat;
  height: 658px;
  width: 100%;
  background-size: cover;
  background-position: 20%;
  background-color: #e5e5e7;
  overflow: hidden;
  position: relative;
}

.control-container .scroller {
  position: relative;
  height: 658px;
  /* z-index: 2; */
  transition: 0.3s cubic-bezier(1, 0, 0, 1);
  transform: translateX(90%);
}
.control-container .scroller h1 {
  color: #b71540;
}

.control-container .scroller .others {
  /* left: -114px; */
}
.control-container .scroller .others,
.control-container .scroller .derivers {
  position: absolute;
  /* top: 40%; */
  font-weight: 700;
  text-transform: uppercase;
  /* transform: rotate(-90deg); */
  margin: -10%;
  font-size: 36px;
  text-shadow: 0 2px 5px gray;
}
.control-container .scroller .derivers {
  z-index: 1;
  /* left: -47px;s */
}
.control-container .scroller .dragger {
  z-index: 1;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  left: -16px;
  bottom: 8px;
  width: 40px;
  height: 40px;
  background: #fff;
  box-shadow: -4px 0 22px;
  border-radius: 50%;
  cursor: pointer;
  border: none;
  transition-duration: 0.2s;
  outline: none;
}
.control-container .scroller > img {
  -webkit-filter: blur(3px);
  filter: blur(3px);
  /* width: 100%; */
  box-shadow: -4px 0 22px;
  height: 100%;
}
.control-container .scroller .dragger.slided {
  transform: rotate(180deg);
}

