/* // Styling */
.slider77 {
  background: white;
  box-shadow: 0 10px 20px -5px rgba(0, 0, 0, 0.125);
  height: 100px;
  margin: auto;
  overflow: hidden;
  position: relative;
  /* top: 25rem; */
  width: 95%;
}

.slider77 ::before,
.slider77 ::after {
  /* @include white-gradient; */
  content: "";
  height: 100px;
  position: absolute;
  width: 200px;
  /* // z-index: 2; */
}

.slider77 ::after {
  right: 0;
  top: 0;
  transform: rotateZ(180deg);
}

.slider77 ::before {
  left: 0;
  top: 0;
}
/* // Animation */
@keyframes scroll1 {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(calc(-250px * 7));
  }
}
.slide-track {
  animation: scroll1 40s linear infinite;
  display: flex;
  width: calc(250px * 14);
}

.slide55 {
  height: 100px;
  width: 250px;
}

.float56 {
  background-color: mediumaquamarine;
  color: white;
  padding: 11px 20px;
  border-radius: 50%;
  cursor: pointer;
  opacity: 0.8;
  position: fixed;
  bottom: 23px;
  right: 28px;
  width: 3rem;
  z-index: 99;
}
